header {
    background-color: rgb(29,29,29);
    padding: 1% 0% 1% 0;
}
.navbar {
    font-size: 1.7rem;
    display: inline;
    text-align: center;
}

.nav-p {
    margin: 3px 0 3px 0;
}

.nav-img {
    display: inline;
    margin-left: 1%;
    padding: 0 1% 0 0;
    max-width: 20px;
}

.scores {
    background-color: rgba(29,29,29, .8);
    padding: 1px 10px 3px 10px;
    position: fixed;
    right: .5%;
    border-radius: 5px;
}

@media only screen and (max-width: 666px) {
    .nav-img {
        padding: 2px 2% 0 0;
    }
    .navbar {
        font-size: 1.3rem;
    }
    .nav-p {
        margin: 15px 0 15px 0;
    }
  }